$primary-color: #fd512f;
$font-family: 'Manrope';
$heading-font-family: 'Poppins', sans-serif;
:root {
  --ldp-primary-color: #{$primary-color};
  --ldp-font-family: #{$font-family};
  --ldp-heading-font-family: #{$heading-font-family};
}
@mixin close-btn {
  .el-dialog__header {
    padding: 0;

    .closep {
      background-color: var(--ldp-primary-color);
      border-radius: 50px;
      top: -20px;
      right: -20px;
      z-index: 10;
      opacity: 1;
    }
  }
}

@mixin gateways-list {
  .wpcm-payment-gateways {
    ul {
      display: inline-flex;
      flex-wrap: wrap;
      li > a {
        padding: 12px 25px;
        border: 2px solid #d3d3d3;
        display: block;
        margin: 10px 10px 0 0;
        background: #fff;
        border-radius: 0;
        font-family: var(--ldp-font-family);
        > span img {
          margin-right: 10px;
        }
        &.active, &:hover {
          background-color: var(--ldp-primary-color);
          border-color: var(--ldp-primary-color);
          color: #fff;
          img {
            filter: brightness(0) invert(1);
          }
        }
      }
    }
  }
}

@mixin el-custom-dropdown($shadow) {
  @if $shadow {
    box-shadow: 0 0 10px rgb(0 0 0 / 20%);
  } @else {
    box-shadow: none;
    border: 2px solid var(--ldp-primary-color);
  }
  padding: 10px 30px;
  height: 70px;
}

.px-100, .pl-100 {
    padding-left: 100px;
}
.px-100, .pr-100 {
    padding-right: 100px;
}
.my-50, .mb-50 {
    margin-bottom: 50px;
}
.my-50, .mt-50 {
    margin-top: 50px;
}

.rounded-md {
    border-radius: .5rem;
}

